/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  color: #ffffff;
  font-family: "Roboto", sans-serif;
}

@media (min-width: 1024px) {
  body {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.7rem;
}

h3 {
  font-size: 2.2rem;
}

h4 {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

.Enlace-Activo {
  font-weight: bold;
}

/* Build */
body {
  margin: 0;
  height: 100vh;
  background: radial-gradient(circle at 20% 50%, #5064e7 0%, #010d40 40%), radial-gradient(circle at 80% 50%, #ff0022 0%, #010d40 50%), linear-gradient(135deg, transparent, rgba(255, 0, 34, 0.3) 70%);
  background-blend-mode: screen;
  background-color: #010d40;
  background-size: 150% 150%;
  background-repeat: no-repeat;
  animation: moveGradient 20s ease infinite;
}

@keyframes moveGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgba(255, 255, 255, 0.05); /* Color blanco transparente */
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); /* Sombra suave */
  backdrop-filter: blur(10px); /* Desenfoque */
  -webkit-backdrop-filter: blur(10px); /* Soporte para Safari */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Borde tenue */
  height: 100%;
}
@media (min-width: 1024px) {
  .header {
    justify-content: space-between;
    padding: 2rem;
    margin: 2rem;
    height: 95%;
    width: 95%;
  }
}

.Contact-info {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;
}
@media (min-width: 1024px) {
  .Contact-info {
    font-size: 2.5rem;
  }
}

@keyframes colorPulse {
  0% {
    background-position: 0% 50%;
    filter: brightness(1) saturate(1);
    transform: scale(1);
  }
  25% {
    filter: brightness(1.2) saturate(1.3);
    transform: scale(1.02);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.1) saturate(1.6);
    transform: scale(1.01);
  }
  75% {
    filter: brightness(1.3) saturate(1.4);
    transform: scale(1.03);
  }
  100% {
    background-position: 0% 50%;
    filter: brightness(1) saturate(1);
    transform: scale(1);
  }
}
.title {
  text-align: center;
  font-size: clamp(4rem, 8vw, 5rem);
  font-family: var(--TittleFont); /* Si usas SCSS reemplaza v.$TittleFont */
  color: transparent;
  background: linear-gradient(-45deg, #ff6ec4, #7873f5, #1fd1f9, #f9f871, #ff4e00, #ff6ec4);
  background-size: 500% 500%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: colorPulse 5s ease-in-out infinite;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.2);
  user-select: none;
}
@media (min-width: 1024px) {
  .title {
    font-size: 10rem;
  }
}

.tittle-span {
  color: transparent;
  background: linear-gradient(45deg, #ff4e00, #ffe600, #00f0ff, #e600ff, #ff4e00);
  background-size: 600% 600%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: colorPulse 6s ease-in-out infinite;
  text-shadow: 0 0 6px rgba(255, 255, 0, 0.4);
}

.Barra {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .Barra {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}
.Barra .Barra_Eslogan {
  display: flex;
  justify-content: center;
  align-items: baseline;
  gap: 1rem;
}
.Barra .Barra_Eslogan img {
  width: 3rem;
}
.Barra .Barra_Navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .Barra .Barra_Navegacion {
    flex-direction: row;
    justify-content: space-between;
    gap: 2rem;
    width: 50%;
  }
}
.Barra .Barra_Navegacion .MainNavegation-Link {
  color: #ffffff;
  font-size: 2rem;
}
.Barra .Barra_Navegacion .MainNavegation-Link:hover {
  color: #dcdce0;
}