@import url(https://fonts.googleapis.com/css2?family=Bungee+Spice&display=swap);

$FontFamily: "Roboto", sans-serif;
$TittleFont: "Bungee Spice", sans-serif;
$telefono: 480px;
$tablet: 768px;
$desktop: 1024px;

$Primary-Blue: #5064e7; // Azul eléctrico (acento principal)
$Primary-Red: #ff0022; // Rojo carmesí (acento secundario)
$Deep-Blue: #010d40; // Azul oscuro (fondo base)
$Dark-Navy: #0a0f2a; // Fondo para secciones o navbars
$Dark-Gray: #1a1a1f; // Fondos alternos o modales
$Soft-Gray: #2e2e38; // Bordes, inputs, elementos secundarios
$White: #ffffff; // Texto principal
$Off-White: #dcdce0; // Texto secundario o párrafos
$Light-Blue: #8faeff; // Enlaces o hover azules
$Alert-Red: #ff4a4a; // Estados de error o advertencia
$Highlight-Red: #ff3366; // Hover rojo o elementos destacados
$Yellow: #f5f79c;
// Estados o detalles sutiles
$Shadow-Color: rgba(0, 0, 0, 0.4); // Sombra sutil
$Overlay-Dark: rgba(0, 0, 0, 0.7); // Overlays
