@use "global/var" as v;
@use "global/mixins" as m;

.Barra {
  display: flex;
  flex-direction: column;

  @include m.desktop {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .Barra_Eslogan {
    display: flex;
    justify-content: center;
    align-items: baseline;

    gap: 1rem;
    img {
      width: 3rem;
    }
  }

  .Barra_Navegacion {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    @include m.desktop {
      flex-direction: row;
      justify-content: space-between;
      gap: 2rem;
      width: 50%;
    }

    .MainNavegation-Link {
      color: v.$White;
      font-size: 2rem;
      &:hover {
        color: v.$Off-White;
      }
    }
  }
}
