@use "global/var" as v;
@use "global/mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  color: v.$White;
  font-family: v.$FontFamily;
}

body {
  @include m.desktop {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2.7rem;
}
h3 {
  font-size: 2.2rem;
}
h4 {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

.Enlace-Activo {
  font-weight: bold;
}
