@use "global/var" as v;
@use "global/mixins" as m;

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  background: rgba(255, 255, 255, 0.05); /* Color blanco transparente */
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1); /* Sombra suave */
  backdrop-filter: blur(10px); /* Desenfoque */
  -webkit-backdrop-filter: blur(10px); /* Soporte para Safari */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Borde tenue */
  height: 100%;
  @include m.desktop {
    justify-content: space-between;
    padding: 2rem;
    margin: 2rem;
    height: 95%;
    width: 95%;
  }
}

.Contact-info {
  display: flex;
  justify-content: space-between;
  font-size: 1rem;

  @include m.desktop {
    font-size: 2.5rem;
  }
}

@keyframes colorPulse {
  0% {
    background-position: 0% 50%;
    filter: brightness(1) saturate(1);
    transform: scale(1);
  }
  25% {
    filter: brightness(1.2) saturate(1.3);
    transform: scale(1.02);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.1) saturate(1.6);
    transform: scale(1.01);
  }
  75% {
    filter: brightness(1.3) saturate(1.4);
    transform: scale(1.03);
  }
  100% {
    background-position: 0% 50%;
    filter: brightness(1) saturate(1);
    transform: scale(1);
  }
}

.title {
  text-align: center;
  font-size: clamp(4rem, 8vw, 5rem);
  font-family: var(--TittleFont); /* Si usas SCSS reemplaza v.$TittleFont */
  color: transparent;
  background: linear-gradient(
    -45deg,
    #ff6ec4,
    #7873f5,
    #1fd1f9,
    #f9f871,
    #ff4e00,
    #ff6ec4
  );
  background-size: 500% 500%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: colorPulse 5s ease-in-out infinite;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.2);
  user-select: none;

  @include m.desktop {
    font-size: 10rem;
  }
}

.tittle-span {
  color: transparent;
  background: linear-gradient(
    45deg,
    #ff4e00,
    #ffe600,
    #00f0ff,
    #e600ff,
    #ff4e00
  );
  background-size: 600% 600%;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: colorPulse 6s ease-in-out infinite;
  text-shadow: 0 0 6px rgba(255, 255, 0, 0.4);
}
